class SelectCity{
    constructor(){
        this.value = [];
        
        this.selectProvince = new Select({ title : '' });
        this.selectProvince.setRenderMap({
            name : 'n',
            key  : 'k'
        })
        this.selectCity = new Select({ title : '' , stopRenderKey: true});
        this.selectCity.setRenderMap({ name : 'n'});
        this.selectProvince.setData(CITY_DATA);
        this.selectProvince.onTap( item => {
            this.value[0] = item;
            let cityData = this.getCityData(item.c);
            if (cityData.length === 1) {
                this.value[1] = cityData[0];
                if (this.onTapEvent) {
                    this.onTapEvent.call(this, this.value);
                    this.selectProvince.hide();
                }
            }else{
                // 
                this.selectCity.setData(this.getCityData(item.c));
                this.selectCity.show();
            }
            
        });
        this.selectCity.onTap( item => {
            this.value[1] = item;
            if (this.onTapEvent) {
                this.onTapEvent.call(this, this.value);
            }
            this.selectProvince.hide();
            this.selectCity.hide();
        });

        this.onTapEvent = null;
    }
    onTap(fn){
        this.onTapEvent = fn;
        return this;
    }
    getCityData(pID){
        let ret = [];
        CITY_DATA.forEach(function(item) {
            if (item.c == pID) {
                ret = item.cs;
            }
        });
        return ret;
    }
    show(){
        this.selectProvince.show();
        return this;
    }
    hide(){
        this.selectProvince.hide();
        this.selectCity.hide();
        return this;
    }
    destroy(){
        this.selectProvince.destroy();
        this.selectCity.destroy();
        return this;
    }
}

//
let instanceSelectCity ;
SelectCity.getOnly = function(){
    if (!instanceSelectCity) {
        instanceSelectCity = new SelectCity();
    }
    return instanceSelectCity;
};

SelectCity.cityData = CITY_DATA;

module.exports = SelectCity;




